import React, { Component } from "react";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [
        { completed: false, text: "Walk the dog" },
        { completed: false, text: "Pick up the trash" },
        { completed: true, text: "Do the dishes" }
      ]
    };

    this.handleNewItem = this.handleNewItem.bind(this);
  }

  componentDidMount() {
    this.input.focus();
  }

  render() {
    return (
      <div className="m-3">
        <h1>Todo</h1>
        <ul className="list-group">
          {this.state.items.map(item => (
            <li
              key={item.text}
              className="list-group-item"
              style={
                item.completed
                  ? { textDecoration: "line-through", color: "lightgrey" }
                  : {}
              }
            >
              {item.text}
              <button
                onClick={() => {
                  this.handleRemove(item);
                }}
                className="btn btn-danger float-right"
              >
                Remove
              </button>
              <button
                onClick={() => {
                  this.handleDone(item);
                }}
                className="btn btn-success mr-2 float-right"
              >
                Done
              </button>
            </li>
          ))}
        </ul>
        <form className="form-inline" onSubmit={this.handleNewItem}>
          <div className="form-group mb-2">
            <input
              ref={e => (this.input = e)}
              type="text"
              className="form-control"
              placeholder="New item"
            />
          </div>
          <button type="submit" className="btn btn-primary mb-2">
            Confirm identity
          </button>
        </form>
      </div>
    );
  }

  handleNewItem(e) {
    e.preventDefault();

    if (this.input.value == "") {
      return;
    }

    var newItems = this.state.items;
    newItems.push({ text: this.input.value, completed: false });
    this.setState({
      items: newItems
    });

    this.input.value = "";
    this.input.focus();
  }

  handleRemove(itemToRemove) {
    var newItems = this.state.items.filter(i => i != itemToRemove);
    this.setState({
      items: newItems
    });
    console.log(newItems);
  }

  handleDone(itemToBeDone) {
    var updatedItems = this.state.items;
    var index = updatedItems.indexOf(itemToBeDone);
    updatedItems[index].completed = true;
    this.setState({
      items: updatedItems
    });
    console.log(index);
  }
}

export default App;
